import { useState, useEffect } from 'react';
import Form from 'components/Form';
import { getDataFromApiSearchByName } from 'components/Api/movies-service';
import SearchList from 'components/SearchList';
import ErrorImage from 'components/ErrorImage';

const Movies = () => {
  const [value, setValue] = useState('');
  const [resultsSearch, setResultsSearch] = useState([]);

  // –û—Ç—Ä–∏–º–∞–Ω–Ω—è –¥–∞–Ω–Ω–∏—Ö –∑ "–ë–µ–∫–µ–Ω–¥—É"
  useEffect(() => {
    const fethSearch = async () => {
      if (!value) return;
      try {
        const { results } = await getDataFromApiSearchByName(value);
        setResultsSearch(results);
      } catch (error) {
        console.log(error.message);
      }
    };
    fethSearch(value);
  }, [value]);

  // –û—Ç—Ä–∏–º–∞–Ω–Ω—è –∑–Ω–∞—á–µ–Ω–Ω—è –∑ "–Ü–Ω–ø—É—Ç—É"
  const onSubmit = query => {
    setValue(query);
  };

  return (
    <div>
      <Form onSubmit={onSubmit} />
      {resultsSearch.length === 0 && value !== '' ? (
        <ErrorImage />
      ) : (
        <SearchList resultsSearch={resultsSearch} />
      )}
    </div>
  );
};

export default Movies;
